.input {
  padding: 7px 14px;

  font-family: inherit;
  font-size: inherit;

  border-radius: $border-radius;
  border: $border-gray;
  outline: none;


  &_fullwidth {
    width: 100%;
  }

  &:focus {
    border-color: $color-primary;
  }
}

.input-submit {
  padding: 7px 14px;
  background: none;
  font-family: inherit;
  font-size: inherit;
  color: rgba(59, 65, 75, .8);
  border-radius: $border-radius;
  border: $border-gray;
  outline: none;
  text-align: start;

}

.input-width {
  max-width: 100%;
}

.textarea {
  max-width: 100%;

  padding: 7px 14px;

  font-family: inherit;
  font-size: inherit;

  border-radius: $border-radius;
  border: $border-gray;
  outline: none;
  height: 93px;
}

.items-option-end {
  margin: 10px 0;
}